.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.fa-star{
    position: absolute;
    left: 63px;
    top: 5px;
    font-size: 150%;
    color: yellow;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
    
    &:before {
        position: absolute;
        content: "";
        height: 26px;
        width: 26px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
    }
}

input:checked + .slider {
    background-color: yellow;

    .slider:before{
        background-color: black;
    }
}
  
input:focus + .slider {
    box-shadow: 0 0 1px yellow;
}
  
input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

.slider.round {
    border-radius: 34px;
    
    &:before {
        border-radius: 50%;
    }
}